export default "<template>\r\n\t<section aria-label=\"programmatic b-snackbar examples\" class=\"buttons\">\r\n\t\t<b-button @click=\"launch\">\r\n\t\t\tLaunch Default Snackbar\r\n\t\t</b-button>\r\n\t\t<b-button @click=\"warn\" variant=\"is-warning\">\r\n\t\t\tLaunch Warning Snackbar\r\n\t\t</b-button>\r\n\t\t<b-button @click=\"danger\" variant=\"is-danger\">\r\n\t\t\tLaunch Danger Snackbar\r\n\t\t</b-button>\r\n\t</section>\r\n</template>\r\n<script lang=\"ts\">\r\nimport BButton from 'buetify/lib/components/button/BButton';\r\nimport { useSnackbar, useToast } from 'buetify/lib/composables';\r\nimport { defineComponent } from 'vue';\r\n\r\nexport default defineComponent({\r\n\tname: 'template-snackbar-example',\r\n\tcomponents: {\r\n\t\tBButton\r\n\t},\r\n\tsetup() {\r\n\t\tconst snackbar = useSnackbar();\r\n\t\tconst toast = useToast();\r\n\r\n\t\tfunction onAction() {\r\n\t\t\ttoast.open({\r\n\t\t\t\tvariant: 'is-dark',\r\n\t\t\t\tmessage: 'Action Pressed',\r\n\t\t\t\tposition: 'is-top',\r\n\t\t\t\tshouldQueue: false\r\n\t\t\t});\r\n\t\t}\r\n\r\n\t\tfunction launch() {\r\n\t\t\tsnackbar.open({\r\n\t\t\t\tmessage: `Default, positioned bottom-right with a green 'OK' button`\r\n\t\t\t});\r\n\t\t}\r\n\r\n\t\tfunction warn() {\r\n\t\t\tsnackbar.open({\r\n\t\t\t\tmessage: 'Yellow button and positioned on top, click to close',\r\n\t\t\t\tposition: 'is-top',\r\n\t\t\t\tactionText: 'Retry',\r\n\t\t\t\tvariant: 'is-warning',\r\n\t\t\t\tisIndefinite: true,\r\n\t\t\t\tonAction: onAction\r\n\t\t\t});\r\n\t\t}\r\n\r\n\t\tfunction danger() {\r\n\t\t\tsnackbar.open({\r\n\t\t\t\tmessage:\r\n\t\t\t\t\t'Snackbar with red action, positioned on bottom-left and a callback.<br>Note: <em>Message can include html</em>.',\r\n\t\t\t\tposition: 'is-bottom-left',\r\n\t\t\t\tactionText: 'Retry',\r\n\t\t\t\tvariant: 'is-danger',\r\n\t\t\t\tisIndefinite: true,\r\n\t\t\t\tonAction: onAction\r\n\t\t\t});\r\n\t\t}\r\n\r\n\t\treturn {\r\n\t\t\tlaunch,\r\n\t\t\twarn,\r\n\t\t\tdanger\r\n\t\t};\r\n\t}\r\n});\r\n</script>\r\n";